$move-initial: calc(-25%);
$move-final: calc(-50%);

.marquee {
  position: relative;
  overflow: hidden;
  .marquee__inner {
    width: fit-content;
    display: flex;
    position: relative;
    transform: translate3d($move-initial, 0, 0);
    animation: marquee 5s linear infinite;
  }
  .row-title {
    padding: 0 8vw;

  }
}
@keyframes marquee {
  0% {
    transform: translate3d($move-initial, 0, 0);
  }

  100% {
    transform: translate3d($move-final, 0, 0);
  }
}